.leaflet-control {
  z-index: 0 !important;
}
.leaflet-pane {
  z-index: 0 !important;
}
.leaflet-top,
.leaflet-bottom {
  z-index: 0 !important;
}

.marker {
  position: absolute;
  margin-left: -14px;
  margin-top: -40px;
  border-radius: 50% 50% 50% 0;
  height: 30px;
  width: 30px;
  place-items: center;
  display: grid;
  transform: rotate(-45deg);
}

.marker-text {
  color: black;
  background-color: white;
  height: 18px;
  width: 18px;
  border-radius: 18px;
  line-height: 18px;
  text-align: center;
  transform: rotate(45deg);
  font-size: 9px;
  font-weight: bold;
}
